 // post请求
import {publicing} from '../../api/api.js'
 // 请求地址
import {wxloginurl} from '../../api/request.js'
//引入类
let logins = require('../../login/login.js')
var {log} = console
export default{
	data(){
		return{
			wxlogin: true,
			usering:{}
		}
	},
	methods:{
			// 获取头像昵称
					getUserInfo(event){
						log(event)
						let wxing = event.detail.userInfo
						// 实例化类
						const logclass = new logins(wxing)
						// 实例化类之后。可以访问类中的方法和属性
						logclass.listing()
						.then((res)=>{
							log(res)
							this.ifUser()
						})
						.catch((err)=>{
							log(err)
						})
		
					},
					
					// 是否已登录
					ifUser(){
						let setdata = uni.getStorageSync('usermen')
						if(!setdata){
							this.wxlogin = false
						}else{
							this.wxlogin = true
							this.usering = setdata
						}
					}
					
				},
				
				onShow() {
					log('每次都执行')
					this.ifUser()
				}
}
